import { Component } from '@angular/core';

@Component({
  selector: 'bb-infinite-scroll-example-ui',
  templateUrl: './infinite-scroll.example.component.html',
})
export class InfiniteScrollExampleComponent {
  items: number[] = [1, 2, 3, 4];
  complete = false;
  loading = false;

  protected maxCount = 42;

  loadMore() {
    if (!this.loading) {
      this.loading = true;

      window.setTimeout(() => {
        this.loading = false;
        this.complete = this.items.length >= this.maxCount;

        this.generateMore(5);
      }, 1000);
    }
  }

  generateMore(count: number) {
    const length = Math.max(
      0,
      Math.min(this.maxCount, this.items.length + count) -
        this.items.length,
    );
    const start = 1 + (this.items[this.items.length - 1] || 0);

    console.log(start, length);
    this.items = [
      ...this.items,
      ...new Array(length).fill(0).map((_, index) => start + index),
    ];
  }
}
