import { Component } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';

@Component({
  selector: 'bb-input-range-example-ui',
  templateUrl: './input-range.example.component.html',
})
export class InputRangeExampleComponent {
  form = this.formBuilder.group({
    range: this.formBuilder.group(
      {
        min: [0, Validators.min(10)],
        max: [100, Validators.max(90)],
      },
      {
        validator: (g: FormGroup) => {
          const min = (g.get('min') as FormControl).errors;
          const max = (g.get('max') as FormControl).errors;

          const errors = min || max ? { ...min, ...max } : null;

          console.log(errors);

          return errors;
        },
      },
    ),
  });

  constructor(private readonly formBuilder: FormBuilder) {}
}
