import { Component } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { BackbaseDocs } from '@backbase/ui-ang/util';

@Component({
  selector: 'bb-dropdown-multi-select-ui-showcase',
  templateUrl: 'dropdown-multi-select-showcase.component.html',
})
@BackbaseDocs({
  isTheme2Stable: true,
})
export class DropdownMultiSelectShowcaseComponent {
  public flatListDropdownMultiSelect = new FormControl('');
  public parentChildDropdownMultiSelect = new FormControl('');
  public disabledDropdownMultiSelect = new FormControl('');

  public options = [
    { value: 'one', label: 'One' },
    { value: 'two', label: 'Two' },
    { value: 'three', label: 'Three' },
    { value: 'four', label: 'Four' },
    { value: 'five', label: 'Five' },
    { value: 'eleven', label: 'Eleven' },
  ];

  public parentChildOptions = [];

  public form: FormGroup;

  constructor(private fb: FormBuilder) {
    this.form = this.fb.group({
      categories: new FormControl(),
    });
  }

  resetForm() {
    this.form.reset();
  }
}
