import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import {
  CheckboxItem,
  CheckboxList,
  ConfigurationInputService,
  DropdownItem,
  DropdownList,
  RadioGroup,
  RadioGroupList,
} from './configuration-input.service';

@Component({
  selector: 'bb-configuration-input-ui',
  templateUrl: './configuration-input.component.html',
})
export class ConfigurationInputComponent implements OnInit {
  @Input()
  checkboxList: CheckboxList = [];

  @Input()
  radioList: RadioGroupList = [];

  @Input()
  dropdownList: DropdownList = [];

  vform: FormGroup;

  ngOnInit() {
    if (this.dropdownList) {
      this.dropdownList.forEach((dropdownItem: DropdownItem) => {
        this.dropdown.addControl(dropdownItem.formControlName, new FormControl(dropdownItem.dropdownItemList[0]));
      });
    }

    if (this.radioList) {
      this.radioList.forEach((radioItem: RadioGroup) => {
        this.radio.addControl(radioItem.formControlName, new FormControl(radioItem.radioItems[0].value));
      });
    }

    if (this.checkboxList) {
      this.checkboxList.forEach((checkboxItem: CheckboxItem) => {
        this.checkbox.addControl(checkboxItem.formControlName, new FormControl(checkboxItem.value));
      });
    }
    this.configurationInputService.sharedField.next(this.vform.value);

    this.vform.valueChanges.subscribe((val) => {
      this.configurationInputService.sharedField.next(val);
    });
  }
  constructor(
    private readonly formBuilder: FormBuilder,
    private readonly configurationInputService: ConfigurationInputService,
  ) {
    this.vform = this.formBuilder.group({
      checkbox: new FormGroup({}),
      radio: new FormGroup({}),
      dropdown: new FormGroup({}),
    });
  }

  get dropdown(): FormGroup {
    return this.vform.get('dropdown') as FormGroup;
  }

  get radio(): FormGroup {
    return this.vform.get('radio') as FormGroup;
  }

  get checkbox(): FormGroup {
    return this.vform.get('checkbox') as FormGroup;
  }
}
