import { Component } from '@angular/core';
import { BackbaseDocs } from '@backbase/ui-ang/util';

@Component({
  selector: 'bb-infinite-scroll-ui-showcase',
  templateUrl: './infinite-scroll-showcase.component.html',
})
@BackbaseDocs({
  isTheme2Stable: true,
})
export class InfiniteScrollShowcaseComponent {
  items = [1, 2, 3, 4];
  complete = false;
  loading = false;
  private page = 0;

  loadMore() {
    if (!this.loading) {
      this.loading = true;
      this.page = this.page + 1;
      window.setTimeout(() => {
        this.items = Array.from({ length: this.page * 4 + 4 }).map((_, i) => i + 1);
        this.loading = false;
        this.complete = this.page >= 4;
      }, 1000);
    }
  }
}
